import { createRouter, createWebHistory } from 'vue-router'
import store from '../store'

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('../views/Home.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/welcome',
    name: 'welcome',
    component: () => import('../views/Welcome.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/login',
    name: 'login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/register',
    name: 'register',
    component: () => import('../views/Register.vue')
  },
  {
    path: '/recycling',
    name: 'recycling',
    component: () => import('../views/Recycling.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/profile',
    name: 'profile',
    component: () => import('../views/Profile.vue'),
    meta: { requiresAuth: true }
  },
  {
    path: '/admin',
    name: 'admin',
    component: () => import('../views/Admin.vue'),
    meta: { requiresAuth: true, requiresAdmin: true }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  const isAuthenticated = store.getters.isAuthenticated

  if (to.meta.requiresAuth && !isAuthenticated) {
    next('/login')
  } else if (isAuthenticated) {
    // ÿ•ÿ∞ÿß ŸÉÿßŸÜ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖ ŸÖÿ≥ÿ¨ŸÑ ÿØÿÆŸàŸÑ
    const userLanguage = localStorage.getItem('userLanguage')
    
    if (to.name === 'welcome') {
      // ÿ•ÿ∞ÿß ŸÉÿßŸÜ ŸÅŸä ÿµŸÅÿ≠ÿ© ÿßŸÑÿ™ÿ±ÿ≠Ÿäÿ® ŸàÿßÿÆÿ™ÿßÿ± ÿßŸÑŸÑÿ∫ÿ© ŸÖÿ≥ÿ®ŸÇÿßŸãÿå ÿßÿ∞Ÿáÿ® ŸÑŸÑÿ±ÿ¶Ÿäÿ≥Ÿäÿ©
      if (userLanguage) {
        next('/')
      } else {
        next()
      }
    } else {
      // ÿ•ÿ∞ÿß ŸÉÿßŸÜ Ÿäÿ≠ÿßŸàŸÑ ÿßŸÑÿØÿÆŸàŸÑ ŸÑÿ£Ÿä ÿµŸÅÿ≠ÿ© ÿ£ÿÆÿ±Ÿâ ŸàŸÑŸÖ ŸäÿÆÿ™ÿ± ÿßŸÑŸÑÿ∫ÿ© ÿ®ÿπÿØÿå ÿßÿ∞Ÿáÿ® ŸÑÿµŸÅÿ≠ÿ© ÿßŸÑÿ™ÿ±ÿ≠Ÿäÿ®
      if (!userLanguage && to.name !== 'welcome') {
        next('/welcome')
      } else {
        next()
      }
    }
  } else {
    next()
  }
})

export default router